(function() {
  'use strict';

  angular
      .module('neduTreeBrowser')
      .factory('ntbRecursionHelper', ntbRecursionHelper);

  /** @ngInject */
  function ntbRecursionHelper($compile) {

    return {
        /**
         * Manually compiles the element, fixing the recursion loop.
         * @param element
         * @param [link] A post-link function, or an object with function(s) registered via pre and post properties.
         * @returns An object containing the linking functions.
         */
        compile: function(element, link){
            // Normalize the link parameter
            if(angular.isFunction(link)){
                link = { post: link };
            }

            // Break the recursion loop by removing the contents
            var contents = element.contents().remove();
            var compiledContents;
            return {
                pre: (link && link.pre) ? link.pre : null,
                /**
                 * Compiles and re-adds the contents
                 */
                post: function(scope, element){
                    // Compile the contents
                    if(!compiledContents){
                        compiledContents = $compile(contents);
                    }
                    // Re-add the compiled contents to the element
                    compiledContents(scope, function(clone){
                        element.append(clone);
                    });

                    // Call the post-linking function, if any
                    if(link && link.post){
                        link.post.apply(null, arguments);
                    }
                }
            };
        }
    };

  }

})();
(function() {
  'use strict';

  angular
      .module('neduTreeBrowser')
      .factory('neduTreeBrowser', neduTreeBrowser);

  /** @ngInject */
  function neduTreeBrowser(ntbConfig, $resource, $log, ListSort) {

    var ntbFactory = function (config) {

      var ntb = this;

      ntb.config = {};
      angular.extend(ntb.config, ntbConfig, config);


      /**
      * folder contents
      */

      ntb.currentFolder = {
        id: null,
        parent: null,
        elements: {
          folders: [],
          items: []
        }
      };

      ntb.browseTo = function (id) {

        ntb.currentFolder.id = id;

        var params = {};
        if (typeof ntb.config.endpoints.folder.params !== "undefined") {
          params = angular.extend(params, ntb.config.endpoints.folder.params);
        }
        params = angular.extend(params, {id: id});

        $resource(ntb.config.endpoints.folder.url, params, { query: { isArray: false }})
        .query()
        .$promise
        .then(
          function (resp) {
            ntb.currentFolder.elements.folders = resp.folders.sort(ListSort.sortByKey('name', 1));
            ntb.currentFolder.elements.items = resp.items.sort(ListSort.sortByKey('name', 1));
            ntb.currentFolder.parent = resp.parent_id;
          },
          function (resp) {
            $log.debug("Error loading folder", resp);
          }
        );

      };

      /**
       * folders tree
       */

      ntb.folderTree = [];

      var getFoldersTree = function () {

        if (typeof ntb.config.endpoints.tree !== "undefined") {

          var params = {};
          if (typeof ntb.config.endpoints.tree.params !== "undefined") {
            params = angular.extend(params, ntb.config.endpoints.tree.params);
          }

          $resource(ntb.config.endpoints.tree.url, params)
            .query()
            .$promise
            .then(
              function (resp) {
                ntb.folderTree = resp;
                ntb.browseTo(resp[0].id);
              },
              function (resp) {
                $log.debug("Error loading folder tree", resp);
              }
            );

        } else {

          ntb.folderTree = ntb.config.rawData.tree;
          if (ntb.config.rawData.tree.length && ntb.config.rawData.tree[0].id) {
            ntb.browseTo(ntb.config.rawData.tree[0].id);
          }

        }
      };

      getFoldersTree();

      return ntb;

    };

    return ntbFactory;

  }

})();

angular.module( 'neduTreeBrowser')
.service('ListSortType', function () {
  return {
    SIMPLE_SORT: 0,
    USER_FRIENDLY_SORT: 1,
    STATUS_SORT: 2,
    TAGS_SORT: 3
  };
});

angular.module( 'neduTreeBrowser')
.controller()
.service( 'ListSort', ["ListSortType", function (ListSortType) {
  return {
    sortRules: {
      breadcrumbs: ListSortType.USER_FRIENDLY_SORT,
      username: ListSortType.USER_FRIENDLY_SORT,
      name: ListSortType.USER_FRIENDLY_SORT,
      id: ListSortType.USER_FRIENDLY_SORT,
      created_by: ListSortType.USER_FRIENDLY_SORT,
      modified_by: ListSortType.USER_FRIENDLY_SORT,
      status: ListSortType.STATUS_SORT,
      tags: ListSortType.TAGS_SORT
    },

    statusesOrder: {},

    sortByKey: function(key, order) {
      var sortType = this.sortRules[key] || ListSortType.SIMPLE_SORT;

      if(sortType === ListSortType.SIMPLE_SORT) {
        return this.simpleSort(key, order);
      }
      if(sortType === ListSortType.USER_FRIENDLY_SORT) {
        return this.userFriendlySort(key, order);
      }
      if(sortType === ListSortType.STATUS_SORT) {
        return this.statusSort(key, order);
      }
      if(sortType === ListSortType.TAGS_SORT) {
        return this.tagsSort(key, order);
      }
    },

    simpleSort: function(key, order) {
      return function(elementA, elementB) {
        var a = elementA[key];
        var b = elementB[key];
        if(!a) {
          return order;
        }
        if(!b) {
          return -order;
        }
        return a.localeCompare(b) * order;
      };
    },

    userFriendlySort: function(key, order) {
      var compare = new Intl.Collator(['en', 'pl'], {
        numeric: true
      }).compare;

      return function(elementA, elementB) {
        return compare(elementA[key], elementB[key]) * order;
      };
    },

    statusSort: function(key, order) {
      var t = this;
      return function(elementA, elementB) {
        var a = elementA[key];
        var b = elementB[key];
        var orderA = t.statusesOrder[a] || 0;
        var orderB = t.statusesOrder[b] || 0;

        return (orderA - orderB) * order;
      };
    },

    tagsSort: function(key, order) {
      return function(elementA, elementB) {
        var a = elementA[key];
        var b = elementB[key];
        if(!a || !a.length) {
          return order;
        }
        if(!b || !b.length) {
          return -order;
        }

        return a.join(' ').localeCompare(b.join(' ')) * order;
      };
    }
  };
}]);

(function() {
  'use strict';

  angular
    .module('neduTreeBrowser')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope, neduTreeBrowser) {
    var vm = this;


    vm.endpoints = [
      { id: 1,
        url1: "/fixtures/tree.json",
        url2: "/fixtures/:id.json",
        params: {}
      },
      { id: 2,
        url1: "/fixtures/:param/tree.json",
        url2: "/fixtures/:param/:id.json",
        params: { param: "test" }
      }
    ];

    vm.selectedEndpoint = vm.endpoints[0];

    vm.changeEndpoints = function () {

      vm.ntbConfig.endpoints = {
        tree: {
          url: vm.selectedEndpoint.url1,
          params: vm.selectedEndpoint.params
        },
        folder: {
          url: vm.selectedEndpoint.url2,
          params: vm.selectedEndpoint.params
        }
      };

      vm.ntbInstance = new neduTreeBrowser(vm.ntbConfig);
    };

    vm.displayTypes = [
      { id: 1,
        name: "list"
      },
      { id: 2,
        name: "table"
      }
    ];

    vm.selectedDisplayType = vm.displayTypes[0];

    vm.changeDisplayType = function () {
      vm.ntbConfig.displayAs = vm.selectedDisplayType.name;

      vm.ntbInstance = new neduTreeBrowser(vm.ntbConfig);
    };

    vm.addColumn = function () {
      vm.ntbConfig.columns.push({ key: "", i18n: "", hasIcon: false, hasLink: false});
    };

    vm.removeColumn = function (index) {
      vm.ntbConfig.columns.splice(index, 1);
    };

    vm.ntbConfig = {
      endpoints: {
        tree: {
          url: "/fixtures/tree.json",
          params: {}
        },
        folder: {
          url: "/fixtures/:id.json",
          params: {}
        }
      },
      displayAs: "list",
      columns: [
        { key: "id", i18n: "Id" },
        { key: "name", i18n: "Name", hasIcon: true, hasLink: true }
      ],
      selectionCallback: function (item) {
        console.log(item.id, item.name, "has been selected");
      },
      buttons: [
        {
          class: "btn btn-primary btn-xs",
          icon: "glyphicon glyphicon-pencil",
          text: "Edit",
          action: function () { console.log("Edit clicked!"); }
        },
        {
          class: "btn btn-primary btn-xs",
          icon: "glyphicon glyphicon-eye-open",
          text: null,
          action: function () { console.log("Preview clicked!"); }
        }
      ]
    };


    vm.ntbInstance = new neduTreeBrowser(vm.ntbConfig);


  }
})();

